header {
  position: fixed;
  z-index: 1;
  width: 100%;
  background-color: #171916;
}

header nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 1rem;
}

header nav h1 {
  font-size: 2rem;
  color: #bc3c37;
  -ms-flex-item-align: end;
      align-self: flex-end;
  margin: 0;
}
